/* Header css */

.nav-main {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  width: 100%;
  height: 50px;
  background-color: #047BD5;
  border: 1px solid #047BD5;
}

.nav-link {
  text-decoration: none;
  padding: 2px 10px;
  font-size: 1.2rem;
  color: white;
}



.dropdown {
  position: relative;
  display: inline-block;
  font-family: 'Lato', 'Open Sans', sans-serif;
}

.dropbtn {
  display: flex;
  background-color: #047BD5;
  color: white;
  border: 1px solid #047BD5;
  font-size: 1.2rem;
  padding: 2px 10px;
  cursor: pointer;
  font-family: 'Lato', 'Open Sans', sans-serif;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  z-index: 1;
  font-family: 'Lato', 'Open Sans', sans-serif;
}

.dropdown-content a {
  color: grey;
  padding: 12px 16px;
  display: block;
  text-decoration: none;
  font-family: 'Lato', 'Open Sans', sans-serif;
}



.dropdown:hover .dropdown-content {
  display: block;
}

.fa-chevron-down {
  margin-left: 5px;
}

.nav-dashboard {
  background-color: white;
  color: grey;
}


/*  signup css */

.Signup-container {
  display: flex;
  width: 100%;
  height: 100%;
  margin: 20px 0px;
  align-items: center;
  justify-content: center;
}

.Signup-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 400px;
  background-color: white;

}

.Signup-input {
  margin: 4px 0px;
  padding: 0px 15px;
  width: 200px;
  height: 40px;
}

.Signup-btn {
  border: 1px solid rgb(12, 145, 223);
  margin: 3px 0px;
  width: 200px;
  height: 40px;
  background-color: rgb(12, 145, 223);
  color: white;
}

.signup-text {
  margin: 3px 0px;
}

.Signup-input::placeholder {
  text-align: center;
  color: grey;
  font-family: 'Lato', 'Open Sans', sans-serif;
  font-style: italic;
  font-size: 14px;

}

.Signup-input:focus::placeholder {
  color: #666;
  /* Focused placeholder text color */
  font-style: normal;
  /* Change back to normal font style */
}



